<template>
	<div class="v-showbox">
		<div class="v-title">
			<h3>-</h3>
		</div>
		<div class=v-allmap id=v-allmap>
			...
		</div>
	</div>
</template>
<script>
	let posiIco = require('../../assets/img/posi.png')
	export default {
		name: '',
		data() {
			return {}
		},
		mounted() {
			// API
			// Map
			var map = new BMap.Map("v-allmap", {enableMapClick: true})
			map.centerAndZoom(new BMap.Point(121.443532, 31.24603), 15) //
			map.enableScrollWheelZoom(true) //
			map.setMapStyle({style:'grayscale'}) //
			//map.enableScrollWheelZoom(true);  //

			function showPoly(pointList) {

				//
				for (let c = 0; c < pointList.length; c ++) {
					//
					var myPosIcon = new BMap.Icon(posiIco, new BMap.Size(19, 25))
					//var marker = new BMap.Marker(pointList[c]);
					var marker = new BMap.Marker(pointList[c], { icon: myPosIcon })
					map.addOverlay(marker); //marker
					marker.setAnimation(BMAP_ANIMATION_DROP) //marker
					//  
					var label = new BMap.Label('', {
						offset: new BMap.Size(20, -2) //
					});
					label.setStyle({
						color: '#fff',
						backgroundColor: 'rgba(0, 0, 0, 0.6)',
						'border-radius': '10px',
						border: 0,
						padding: '4px 5px',

					})
					marker.setLabel(label)
				}

				var group = Math.floor(pointList.length / 11)
				var mode = pointList.length % 11;

				var driving = new BMap.DrivingRoute(map, {
					onSearchComplete: function (results) {
						if (driving.getStatus() == BMAP_STATUS_SUCCESS) {
							var plan = driving.getResults().getPlan(0);
							var num = plan.getNumRoutes();
							//alert("plan.num " + num);
							for (var j = 0; j < num; j++) {
								var pts = plan.getRoute(j).getPath(); //
								var polyline = new BMap.Polyline(pts,  {strokeColor: '#f83d3d', strokeWeight:6, strokeOpacity:0.8});
								map.addOverlay(polyline);
							}
						}
					}
				})
				for (var i = 0; i < group; i++) {
					var waypoints = pointList.slice(i * 11 + 1, (i + 1) * 11);
					//11-1 **
					driving.search(pointList[i * 11], pointList[(i + 1) * 11 - 1], {
						waypoints: waypoints
					}) //waypoints
				}
				if (mode != 0) {
					var waypoints = pointList.slice(group * 11, pointList.length - 1); //search
					driving.search(pointList[group * 11], pointList[pointList.length - 1], {
						waypoints: waypoints
					})
				}

			}

			//28
			var p1 = new BMap.Point(121.443532, 31.24603)
			var p2 = new BMap.Point(121.481477, 31.240103)
			var p3 = new BMap.Point(121.493262, 31.237015)
			var p4 = new BMap.Point(121.494987, 31.230099)
			var p5 = new BMap.Point(121.489382, 31.225034)

			var arrayList = []
			arrayList.push(p1)
			arrayList.push(p2)
			arrayList.push(p3)
			arrayList.push(p4)
			arrayList.push(p5)

			//
//			setTimeout( () => {
//				var b = new BMap.Bounds(p1, p5)
//				try {
//					BMapLib.AreaRestriction.setBounds(map, b)
//				} catch (e) {
//					alert(e)
//				}
//			}, 500)

			//
			showPoly(arrayList)
		}
	}
</script>
<style scoped lang="less">
	.v-showbox{
		width: 96%;
		height: 900px;
		min-height: 900px;
		margin: 0 1.875%;
		margin-bottom: 50px;
		padding-bottom: 5px;
		//border: 1px solid red;
		background: url(../../assets/img/shadow-bg.png) no-repeat;
		background-size: 100% 100%;
		position: absolute;
		left: 0px;
		top: 222px;
		display: flex;
		flex-direction: column;
		align-items: center;

		.v-allmap{
			flex: 1;
			width: 99%;
			height: 99%;
			margin-top: 5px;
			color: #fff;
		}

		.v-title{
			width: 100%;
			height: 80px;
			line-height: 80px;
			z-index: 1;
			text-align: center;
			background: url(../../assets/img/show-title.png) no-repeat center;
			h3{
				font-size: 24px;
				color: #00ffd6;
			}
		}
	}

</style>



