<template>
	<el-table
			:data="tableData"
			style="width: 100%">
		<el-table-column
				prop="address"
				label=""
				align="center">
		</el-table-column>
		<el-table-column
				prop="name"
				label=""
				width="140"
				align="center">
		</el-table-column>
		<el-table-column
				prop="name"
				label=""
				width="140"
				align="center">
		</el-table-column>
	</el-table>
</template>

<script>
	export default {
		data() {
			return {
				tableData: [{
					date: '2016-05-02',
					name: '',
					address: ' 1518 '
				}, {
					date: '2016-05-04',
					name: '',
					address: ' 1517 '
				}, {
					date: '2016-05-01',
					name: '',
					address: ' 1519 '
				}, {
					date: '2016-05-03',
					name: '',
					address: ' 1516 '
				}, {
					date: '2016-05-03',
					name: '',
					address: ' 1516 '
				}, {
					date: '2016-05-03',
					name: '',
					address: ' 1516 '
				}]
			}
		}
	}
</script>
<style lang="less">
	.el-table, .el-table__expanded-cell{
		background-color: #051030;
	}
	.el-table tr{
		background: #051030;
		color: #fff;
	}
	.el-table td, .el-table th.is-leaf{
		border-bottom: 1px solid transparent;
	}
	.el-table--enable-row-hover .el-table__body tr:hover>td{
		background-color: #051030;
	}
	table {
		border-collapse: inherit;
	}
	.el-table--border::after, .el-table--group::after, .el-table::before{
		background-color: transparent;
	}
</style>



